.App {
  text-align: center;
}

.App-header,
.App-footer {
  background-color: #282c34;
  min-height: 50px;
  min-width: 100vw;
  position: relative;
  top: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: 15px;
  color: white;
}

.App-footer {
  bottom: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding-top: 10px;
}
.IntroPage {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 75vw;
  align-items: center;
  justify-content: space-around;
  /* background-color: #282c34; */
}

.IntroPage-image {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-around;
  /* padding: 10px; */
}

.img_size {
  width: 35vw;
  height: auto;
}

.IntroPage-currentCity-Box {
  /* border: 1px solid black;
  border-radius: 40px; */
  width: 35vw;
  height: auto;
  display: flex;
  align-items: center;
  justify-content: center;

  /* background-color: #fe4f5a; */
}

.IntroPage-currentCity-data {
  display: flex;
  flex-direction: column;
  align-items: left;
  justify-content: left;
  padding: 20px;
  padding-top: 30px;
  text-align: left;
}

.date-text {
  font-size: 20px;
  font-weight: 400;
  color: #ff4f5a;
}

.timezone-text {
  font-size: 25px;
  font-weight: 600;
}

.degree-text {
  font-size: 40px;
  font-weight: 400;
}

.feels-text {
  padding-top: 10px;
  font-size: 20px;
  font-weight: 400;
}

.weather-more-detail {
  display: flex;
  margin-top: 10px;
  flex-wrap: wrap;
  border-left: 2px solid #ff4f5a;
}

.detail-data {
  padding-left: 10px;
  padding-right: 10px;
}

.exclamation-mark {
  font-size: 100px;
  font-weight: 600;
  color: #ff4f5a;
}

.please-select-city {
  font-size: 20px;
  font-weight: 600;
}

.Location {
  position: absolute;
  top: auto;
  right: 0;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  justify-content: left;
  border-left: 5px solid #ff4f5a;
  height: auto;
  width: 25vw;
}

.select-major-city {
  color: #ff4f5a;
  font-size: 25px;
  font-weight: 600;
  padding: 10px;
}

.Location-scroll {
  position: relative;
  top: 0px;
  overflow: scroll;
  padding-top: 10px;
  padding-bottom: 10px;
  width: 100%;
  height: 30vw;
}

.each-city-button {
  color: #ff4f5a;
  background-color: white;
  border: 2px solid #ff4f5a;
  border-radius: 15px;
  padding: 5px 20px;
  text-align: center;
  text-decoration: none;
}

.each-city-data {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-around;
}
.each-city {
  padding: 5px;
  font-size: 18px;
  width: auto;
}
.App-body {
  display: flex;
  flex-wrap: wrap;
}

.capitalize-text {
  text-transform: capitalize;
}

.demo1 {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-around;
}

.demo {
  width: 40vw;
  height: 30vh;
  background-color: yellow;
}

.social-media-logo {
  width: 35px;
  height: auto;
  padding: 10px;
}

.social-media {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

@media only screen and (max-width: 600px) {
  .Location {
    position: relative;
    top: auto;
    right: 0;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    justify-content: left;
    border: 5px solid #ff4f5a;
    height: auto;
    width: 100%;
  }

  .Location-scroll {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    top: 0px;
    overflow: scroll;
    padding-top: 10px;
    padding-bottom: 10px;
    width: auto;
    height: 40vh;
  }

  .each-city-data {
    position: relative;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
  }

  .each-city {
    padding: 5px;
    font-size: 18px;
    width: auto;
  }
  .img_size {
    width: 100%;
  }

  .IntroPage {
    display: flex;
    flex-direction: column;
    width: 100%;
  }

  .IntroPage-currentCity-Box {
    width: 100%;
  }

  .empty-data {
    padding-bottom: 20px;
  }
}
